import { Component, OnInit } from '@angular/core';

import { AtributoNewDTO } from '../models/fichas/atributo-new.dto';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { tipoRPG, tipoArrayRPG } from '../config/rpg.config';
import { FichaModeloNew } from '../models/fichas/ficha-modelo-new.dto';
import { FichaModeloService } from '../services/fichas/ficha-modelo.service';
import { AlertController } from '@ionic/angular';
import { Router } from '@angular/router';
import { PaginacaoService } from '../services/utils/paginacao.service';

@Component({
  selector: 'app-fichamodelo',
  templateUrl: './fichamodelo.page.html',
  styleUrls: ['./fichamodelo.page.scss'],
})
export class FichamodeloPage implements OnInit {

  abaAtual: string = 'parte1';
  atributos: AtributoNewDTO[] = [];
  display = { show: false, titulo: 'Title', status: [] };
  opcao: number;
  formAtributo: FormGroup;
  formModelo: FormGroup;

  showHelp = false;
  ajudaTemplate = "Exibir Ajuda";

  showAtributos = true;
  iconTable = 'pi pi-minus';

  tipoRPG;
  selectedRPG;

  displayForm: boolean = false;
  listDisplay: boolean = false

  page;

  paginaAtual = 0;

  loading: boolean = false;

  constructor(public formBuilder: FormBuilder,
    private fmService: FichaModeloService,
    private alertCtrl: AlertController,
    private router: Router,
    private paginaService: PaginacaoService) {
    this.formAtributo = this.formBuilder.group({
      nome: ['', [Validators.required]],
      descricao: ['', [Validators.required]],
      bonus: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      nivel: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      imgURL: ['']
    });

    this.formModelo = this.formBuilder.group({
      tipoRPG: ['', [Validators.required]],
      xpAtual: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      xpPra: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      pvAtual: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      pvTotal: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      pmAtual: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      pmTotal: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      karmaAtual: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      nomeFichaModelo: ['', [Validators.required]],
    });

    this.tipoRPG = [
      {
        label: 'Selecione', value: null
      },
      {
        label: tipoArrayRPG[0], value: tipoRPG.MedievalRPG
      },
      {
        label: tipoArrayRPG[1], value: tipoRPG.CyberpunkRPG
      },
      {
        label: tipoArrayRPG[2], value: tipoRPG.SteampunkRPG
      },
      {
        label: tipoArrayRPG[3], value: tipoRPG.HorrorRPG
      }
    ]
    this.selectedRPG = tipoRPG[0];

  }

  async load() {
    const alert = this.alertCtrl.create({
      backdropDismiss: true,

    })
  }

  async salvar() {
    this.loading = true
    let fm: FichaModeloNew = {
      atributos: this.atributos,
      tipoRPG: this.formModelo.get('tipoRPG').value,
      karmaAtual: this.formModelo.get('karmaAtual').value,
      karmaTotal: 100,
      pmAtual: this.formModelo.get('pmAtual').value,
      pmTotal: this.formModelo.get('pmTotal').value,
      pvAtual: this.formModelo.get('pvAtual').value,
      pvTotal: this.formModelo.get('pvTotal').value,
      xpAtual: this.formModelo.get('xpAtual').value,
      xpPra: this.formModelo.get('xpPra').value,
      nomeFichaModelo: this.formModelo.get('nomeFichaModelo').value,
    }

    this.fmService.insert(fm)
      .subscribe(res => { this.loading = false, this.showOk(res.id), console.log(res) },
        error => { this.loading = false, console.log(error) }
      )
  }

  async showDialog(titulo: string, opcao) {
    this.display = {
      show: true,
      titulo,
      status: ['Nome', 'Descrição', 'Nível Inicial', 'Bónus Inicial', 'URL imagem']
    }

    this.opcao = opcao;
  }

  add() {
    this.atributos.push(this.formAtributo.value);
    this.cancelAtributo();
  }

  cancelAtributo() {
    this.display.show = false;
    this.formAtributo.get('nome').setValue('');
    this.formAtributo.get('descricao').setValue('');
    this.formAtributo.get('bonus').setValue('');
    this.formAtributo.get('nivel').setValue('');
    this.formAtributo.get('imgURL').setValue('');
  }

  minimizar() {
    this.showAtributos = !this.showAtributos;
    if (this.showAtributos) {
      this.iconTable = "pi pi-minus";
    } else {
      this.iconTable = "pi pi-plus";
    }
  }

  getAjuda() {
    this.showHelp = !this.showHelp;
    if (this.showHelp) {
      this.ajudaTemplate = "Fechar Ajuda";
    } else {
      this.ajudaTemplate = "Exibir Ajuda";
    }
  }

  ngOnInit() {

  }

  getAtributos() {
    return this.atributos;
  }

  atributoNivel(atributo, opcao: boolean) {
    if (opcao) {
      atributo.nivel++;
    } else {
      atributo.nivel--;
    }
  }

  atributoBonus(atributo, opcao: boolean) {
    if (opcao) {
      atributo.bonus++;
    } else {
      atributo.bonus--;
    }
  }

  async showOk(id) {
    let alert = await this.alertCtrl.create(
      {
        header: 'Sucesso!',
        message: 'Seu modelo foi criado com sucesso! ' + id,
        backdropDismiss: false,
        buttons: [
          {
            text: 'OK',
            handler: () => { this.copyToClipboard(id), this.router.navigateByUrl('gercriacoes'); }
          }
        ]
      }
    );

    alert.present();
  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  novo() {
    this.listDisplay = false
    if (this.displayForm) {
      this.displayForm = false;
    } else {
      this.displayForm = true;
    }
  }

  consultar() {
    //this.toast.clear();
    this.displayForm = false;
    const p = this.fmService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
    this.listar();
  }

  listar() {
    this.listDisplay = true;
  }

  destroyList() {
    this.listDisplay = false;
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

}
